<template>
  <template v-if="hasPreview">
    <div>
      <p>
    <span v-for="(p, index) in previewText.split('\n')" :key="index">
      {{p}}
      <br>
    </span>
      </p>
    </div>
    <div :class="previewClass">
      <span v-for="(p, index) in text.split('\n')" :key="index">
      {{p}}
      <br>
    </span>
    </div>
    <CTAButton :label="buttonLabel" @click="togglePreview"/>
  </template>
  <template v-else>
    <div>
      <span v-for="(p, index) in text.split('\n')" :key="index">
    {{p}}
    <br>
    </span>
    </div>
  </template>
  <br>
</template>

<script>
import CTAButton from "@/components/CTAButton";
export default {
  name: "ContentParagraph",
  components: {CTAButton},
  data() {
    return {
      showPreview: true,
    }
  },
  props: {
    hasPreview: {
      type: Boolean,
      default: false,
    },
    previewText: {
      type: String,
      required: false,
    },
    text: {
      type: String,
      required: true,
    },
  },
  computed: {
    buttonLabel() {
      return this.showPreview ? "קרא עוד..." : "הסתר"
    },
    previewClass() {
      return this.showPreview ? "inf_o collapse" : "inf_o"
    }
  },
  methods: {
    togglePreview() {
      this.showPreview = !this.showPreview;
    }
  }
}
</script>

