div {
  font-size: 28px;
  line-height: 1.5em;
  max-width: 70%;
  text-align: right;
  justify-content: center;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 640px) {
  div {
    max-width: 100%;
    font-size: 25px;
  }
}

.inf_o {
  overflow: hidden;
  transition: max-height 1.5s;
  max-height: 1400px;
}

.collapse {
  max-height: 1px;
}

