  components: {
    SiteFooter,
    MainPage,
    InfoForm,
    TabMenu,
  },
  data() {
    return {
      items: ref([
        {label: 'ראשי', command: () => {this.menuSelect("mainPage")} },
        {label: 'הדרכת הורים', command: () => {this.menuSelect("parentalGuidance")} },
